:root {
  --background-color: #383838;
  --text-color: #fff;
  --color-white: #fff;
  --color-graphite-dark: #2f2f2f;
  --color-graphite: #383838;
  --color-text-secondary: #555;
  --color-bg-light: #f9f9f9;
  --color-bg-hover: #f1f1f1;
  --color-border: silver;
  --color-shadow: #0000000d;
  --color-accent: yellow;
  --container-max-width: 1200px;
}

.header {
  background-color: var(--background-color);
  outline: 1px solid #ff0;
}

.footer {
  outline: 1px solid #ff0;
}

li {
  list-style: none;
}

body {
  color: #000;
  background-color: var(--background-color);
}

.trainer-card__photo {
  border: 2px solid #ff0;
  border-radius: 20px;
  width: 150px;
  margin-left: auto;
  margin-right: auto;

  @media screen and (width >= 1200px) {
    width: 350px;
  }
}

.trainer-card {
  flex-direction: column;
  display: flex;

  @media screen and (width >= 1200px) {
    flex-direction: row;
  }
}

.trainer__flex {
  flex-direction: column;
  display: flex;

  @media screen and (width >= 1200px) {
    margin-left: 20px;
  }
}

.trainers-section__track {
  flex-direction: column;
  gap: 100px;
  padding-left: 0;
  display: flex;
}

.trainer-card__name {
  color: #ff0;
  text-align: center;
  padding-top: 30px;

  @media screen and (width >= 1200px) {
    text-align: left;
  }
}

.trainer-card__text {
  text-align: justify;
  color: #fff;
  max-width: 600px;
  margin-top: 20px;
}

.trainers-section__title {
  text-align: center;
  color: #fff;
  margin-top: 50px;
  margin-bottom: 50px;
}
